import React from 'react';

const OutTable = (props) => {
    // const cols = JSON.parse(localStorage.getItem('cols'));

    return (
        <div className="table-responsive">
            <table className={`table table-striped ${props.data ? "show" : ""}`}>
            {/* <thead>
                    <tr>
                        {cols.map((c) => (
                            <th key={c.key}>{c.name}</th>
                        ))}
                    </tr>
                </thead> */}
            {/* <tbody>
                {props.data.map((r, i) => (
                    <tr key={i}>
                        {cols.map((c) => (
                            <td key={c.key}>{r[c.key]}</td>
                        ))}
                    </tr>
                ))}
            </tbody> */}

            <tbody>
                    {
                        props.data.filter(data => data.length > 0).map((r, i) => (
                            <tr key={i}>
                                {r.filter(x => x !== null).map((a, i) => (
                                    <td key={i}
                                        colSpan={
                                            a==='S GDT H NI'? 6 :
                                                a === "DANH SCH HC SINH NG K D TUYN VNG 2" ? 17 :
                                                    a === "TRNG THPT Nguyn Tri" ? 6 :
                                                        a === "NM HC 2022-2023" ? 17 :
                                                            a === "Ngy sinh" ? 3 :
                                                                a === "im s tuyn vng 1" ? 8 : null

                                        }
                                        rowSpan={
                                            a === "STT" || a === "Trng Tiu hc" || a === "Qun/Huyn" || a === "M hc sinh" || a === "Lp" || a === "H v tn" || a === "Gii" || a === "Ni sinh" || a === "Dn tc" || a === "H khu thng tr" || a === "in thoi lin h" || a === "Ghi ch" ? 2 : ""
                                        }
                                    >{a}</td>
                                ))}
                            </tr>
                        ))
                    }
                </tbody>
        </table>
        </div>
    );
};

export default OutTable;